.App {
  background-image: linear-gradient(to bottom right, red, orange);
  min-height: 100vh;
  display: flex;
}

::-webkit-scrollbar {
  width: 0px;
}

::-webkit-scrollbar-thumb {
  background: rgba(136, 136, 136, 0.281);
}

::-webkit-scrollbar-thumb:hover {
  background: #555;
}
